// Переменные для шрифтов
$font-basic: 700 0.88rem / 1.13rem "Lato", sans-serif;
$font-menu-navigation: 400 1.25rem / 1.25rem "Oswald", sans-serif;
$font-main-heading: 400 2.25rem / 2.25rem "Oswald", sans-serif;
$font-hero-subtitle: 400 0.88rem / 0.88rem "Oswald", sans-serif;
$font-title-mobile-large: 400 2.25rem / 2.25rem "Oswald", sans-serif;
$font-title-mobile-medium: 400 1.5rem / 2.31rem "Oswald", sans-serif;
$font-link-basic: 400 1rem / 1rem "Oswald", sans-serif;
$font-example-stats-summ: 400 1.5rem / 1.5rem "Oswald", sans-serif;
$font-example-stats-total: 400 0.88rem / 1.25rem "Oswald", sans-serif;
$font-contacts-offer: 400 1rem / 1.25rem "Oswald", sans-serif;
$font-form-description: 400 0.88rem / 1.13rem "Oswald", sans-serif;
